import "./index.css";
import Axios from "../../../api/Axios";
// import userdata from "./tempData.json";
import { useCallback, useEffect, useState } from "react";
import Loader from "../../../components/Loader";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";
import edit from "../../../assets/edit.svg";

const axios = new Axios();

const ManageBanners = () => {
  const [banners, setBanners] = useState([])
  const [pending, setPending] = useState(false);
  const navigate = useNavigate();

  const getAllBanners = useCallback(async () => {
    setPending(true)
    let options = {
      method: "GET"
    }
    let result = await axios.callApi("/banner/getAllBanners", options);
    if (result?.status == 200) {
      setPending(false)
      setBanners(result?.data?.data)
    }
    else {
      setPending(false)
      toast.error("Something went wrong while fetching products")
    }
  }, [])

  useEffect(() => {
    getAllBanners()
  }, [])

  return (
    <>
    {pending ? <Loader /> : null}
    <div className="ManageBannersMainClass mt-4">
      <div className="ManageBannersHeader">
        <button onClick={() => navigate(`/add-banner`)} style={{float: "left"}}>+ Add Banner</button>
      </div>
        <table className="table borderd">
          <thead>
            <tr>
              <th>Sr. No.</th>
              <th>Page Link</th>
              <th>Type</th>
              <th>Banner Image</th>
              <th>Edit</th>
            </tr>
          </thead>
              <tbody
                className="text-capitalize"   
              >
                {banners?.map((banner, index) => (
                       <tr>
                        <td>{index + 1}</td>
                        <td><span style={{cursor: "pointer"}} onClick={()=>navigate(banner.pageLink)}>Click here</span></td>
                        <td>{banner.type}</td>
                        <td><img style={{width: "100px", height: "100px"}} src={process.env.REACT_APP_BASE_URL + "/" + banner.imageJson.desktop.image} alt="" /></td>
                        <td>
                          <div onClick={() => navigate(`/edit-banner/${banner.id}`)}>
                            <img src={edit} alt="" />
                          </div>
                        </td>
                      </tr>
                ))}
              </tbody>
        </table>
    </div>
    </>
  );
}

export default ManageBanners;
